import React,{useEffect} from "react"
import ArticleList from "../components/ArticleList"
import articlecontent from "./article-content"
import AddComment from "../components/AddComment"
import NotFoundPage from "../NotFoundPage"
import { useState } from "react"
import CommentsList from "../components/CommentsList"
import UpvoteSection from "../components/UpvoteSection"
const ArticlePage = ({match})=> {
    const name = match.params.name;
    const article = articlecontent.find(article => article.name === name);

     //state
     const[articleInfo, setArticleinfo ]= useState({upvote: 0, comments:[]})
    
     useEffect (()=>{
         const fetchData =async () =>{
         const result = await fetch(`/api/articles/${name}`) 
         const data = await result.json();
         setArticleinfo(data);
         } 
        fetchData(); 
        })

    if(!article) return <NotFoundPage/>
    const othernames = articlecontent.filter(article=> article.name!== name)
 return(
<>
<h1>{article.title}</h1>
<UpvoteSection articleName={name} upvotes={articleInfo.upvote} setArticleInfo={setArticleinfo} /> 

<p> The article currently have {articleInfo.upvote} upvotes </p>
{article.content.map((paragraph,key) =>(
    <p key={key}>{paragraph}</p>
    
))}
<CommentsList comments={articleInfo.comments}/>
<AddComment articleName={name} setArticleInfo={setArticleinfo} />
<h3>Other Articles</h3>
<ArticleList article={othernames}/>
</>
 )
}
export default ArticlePage;